.room-finder {
  width: 380px;
  height: 374px;
  padding: 30px;
  background: #FFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  &__header {
    margin: 10px 0;
  }
}

.room-finder__button {
  width: 100%;
  height: 44px;
  margin-top: 20px;
}