.text-field {
  width: 100%;
  height: 44px;
  margin-top: 10px;
  padding-left: 15px;
  font-size: 14px;
  color: $dark75;
  border: 1px solid $dark25;
  box-sizing: border-box;
  border-radius: 4px;

  &::placeholder {
    color: $dark25;
  }

  &:focus {
    outline: none;
    border: 1px solid $dark50;
  }
}